.brend-history {
	padding: 45px 0 55px;
}

.brend-history__title {
	margin-bottom: 30px;
	h2 {
		margin-bottom: 25px;
	}
}

.brend-history__content {
	margin-bottom: 30px;
	p {
		font-family: $gil;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 2;
		letter-spacing: normal;
		color: #000000;
	}
	& > * {
		&:last-child {
			margin-bottom: 0;
		}
	}
}