.container {
}

.container_large {
	@media (min-width: 1500px) {
		max-width: 1400px;
	}
}

.full-height {
	height: 100%;
}


.list-clr {
	padding: 0;
	margin-top: 0;
	margin-bottom: 0;
	list-style: none;
}

.paddings {
	padding-left: 15px;
	padding-right: 15px;
}

.skew-forward {
	@include skew-forward();
}
.bounce-out {
	@include bounce-out();
}
.shrink-effect {
	@include shrink();
}
.grow-effect {
	@include grow();
}
.ripple-out-effect {
	@include ripple-out();
}
.shutter-hor-effect {
	@include shutter-hor();
}
.rectangle-out-effect {
	@include rectangle-out();
}
.hvr-radial-out-effect {
	@include hvr-radial-out();
}
.link-hov-effect {
	@include link-hov-effect();
}

.search-effect {
	animation-name: search;
	animation-duration: 4s;
	animation-iteration-count: infinite;
}
.search-effect-on-focus {
	&:focus {
		animation-name: search;
		animation-duration: .5s;
		animation-delay: .2s;
		animation-iteration-count: infinite;
	}
}
@keyframes search {
	from {
		transform: rotate(0) scale3d(1, 1, 1);

	}
	25% {
		transform: rotate(-20deg) scale3d(1.1, 1.1, 1.1);
	}
	50% {
		transform: rotate(0) scale3d(1, 1, 1);
	}
	75% {
		transform: rotate(20deg) scale3d(.8, .8, .8);
	}
	to {
		transform: rotate(0) scale3d(1, 1, 1);
	}
}

.background-grey {
	background-color: #f7f7f9;
}


.fancybox-slide--iframe {
	.fancybox-content {
		background-color: transparent;
	}
	iframe {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 900px;
		max-height: 500px;
		margin-left: auto;
		margin-right: auto;
		display: block;
	}
}

.page-top {
	position: fixed;
	bottom: 20px;
	right: 20px;
	padding: 5px;

	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-color: #ed8aac;

	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	transition: ease .2s;
	&:hover {
		text-decoration: none;
		color: #fff;
		transform: scale(1.1, 1.1);
	}
	.fa {
		color: #fff;
		position: relative;
		margin-top: -3px;
	}
}

.margin-b-30 {
	margin-bottom: 30px;
}