.squad {
	padding: 60px 0 90px;
}
.squad__item {
	&:nth-child(even) {
		.squad-member__img {
			float: right;
			margin-right: 0;
			margin-left: 40px;
			@include mlg() {
				margin-left: 20px;
			}
			@include mmd() {
				margin-left: 0;
				margin-right: 0;
				float: none;
			}
		}
		.squad-member__content {
			margin-left: 0;
			// margin-right: 500px;
		}
	}
}