.owl-carousel {
	.owl-nav {
		position: absolute;
		margin: 0;
		top: 50%;
		left: 0;
		width: 100%;
		transform: translate(0, -50%);
		height: 45px;
		[class*='owl-'] {
			position: absolute;
			top: 50%;
			// transform: translate(0, -50%);

			font-size: 30px !important;
			color: #000;
			line-height: 1;
			transition: linear .1s;
			padding: 5px;
			cursor: pointer;
			&:hover, &:focus {
				outline: none;
			}
			&:hover {
				background-color: transparent !important;
			}
		}
		.owl-prev {
			left: 5px;
			// transform: translate(5px, -50%);
			&:hover {
				// transform: translate(0, -50%);
			}
		}
		.owl-next {
			right: 5px;
			// transform: translate(-5px, -50%);
			&:hover {
				// transform: translate(0, -50%);
			}
		}
	}
	.owl-dots {
		position: absolute;
		bottom: -50px;
		left: 50%;
		transform: translate(-50%, 0);
		.owl-dot {
			span {
				width: 20px;
				height: 20px;
				background-color: #dedee0;
			}
			&.active {
				span {
					background-color: #636364;
				}
			}
			&:focus {
				outline: none;
			}
		}
	}
	.owl-svg {
		fill: #000;
		stroke: #000;
	}
}