.button-order {
	padding: 17px 26px 16px;
	border: 1px solid #ed8aac;
	font-size: 12px;
	letter-spacing: 4px;
	line-height: 22px;
	line-height: 1;
	letter-spacing: 4px;
	text-transform: uppercase;
	background: #ed8aac;

	color: #fff;
	&:hover {
		color: #fff;
		text-decoration: none;
	}
}