.titles {
	h1 {
		font-family: AOldTyper;
		font-size: 65px;
		font-weight: 400px;
		line-height: 55px;
		text-align: center;
		color: #000000;
		@include mmd() {
			font-size: 30px;
			line-height: 35px;
		}
	}
	h2 {
		font-family: $aot;
		font-size: 50px;
		font-weight: 400;
		line-height: 55px;
		color: #000000;
		@include mmd() {
			font-size: 35px;
			line-height: 40px;
		}
	}
	p {
		font-family: $gil;
		font-size: 18px;
		font-weight: 500;
		line-height: 22px;
		letter-spacing: 2px;
		color: #000000;
		text-transform: uppercase;
		@include mmd() {
			font-size: 16px;
		}
	}
	& > div {
		font-family: $gil;
		font-size: 14px;
		font-weight: 500;
		// line-height: 1.21;
		line-height: 17px;
		letter-spacing: normal;
		color: #000000;
	}
	& > * {
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.content {
	h2 {
		font-family: $aot;
		font-size: 40px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.38;
		letter-spacing: normal;
		color: #ffffff;
		margin-bottom: 20px;
	}
	h3 {
		font-family: $gil;
		font-size: 22px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.82;
		letter-spacing: 2px;
		color: #000000;
		text-transform: uppercase;
	}
	h4 {

		font-family: $gil;
		font-size: 16px;
		font-weight: 700;
		line-height: 24px;
		color: #000000;
	}
	p {
		font-family: $gil;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.38;
		letter-spacing: 2px;
		text-transform: uppercase;
	}
	// p {
	// 	font-family: $gil;
	// 	font-size: 16px;
	// 	font-weight: 500;
	// 	font-style: normal;
	// 	font-stretch: normal;
	// 	line-height: 1.88;
	// 	letter-spacing: normal;
	// 	text-align: center;
	// 	color: #000000;
	// }
	& > * {
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.titles_white, .content_white {
	h1, h2, h3, h4, h5, h6 {
		color: #fff;
	}
	p {
		color: #fff;
	}
}
