.toggle {
	margin-bottom: 15px;
	padding: 10px 0;
	border-bottom: 1px solid #d6d6d6;
	&.toggle_active {
		.toggles__body {
			// display: block;
		}
		.toggle__button {
			&:before {
				transform: rotate(-180deg);
			}
			&:after {
				transform: rotate(90deg);
			}
		}
	}
	& > * {
		&:last-child {
			margin-bottom: 30px;
		}
	}
}
.toggle__head {
	position: relative;
	padding-right: 30px;
}
.toggle__title {
	font-family: AOldTyper;
	font-size: 20px;
	font-weight: 400;
	line-height: 30px;
	color: #000000;
	margin-bottom: 15px;
}
.toggle__button {
	position: absolute;
	right: 0;
	top: 50%;
	transform: translate(0, -50%);
	cursor: pointer;
	width: 28px;
	height: 28px;
	padding: 5px;
	&:before, &:after {
		content: '';

		position: absolute;
		top: 50%;
		left: 50%;
		background-color: #ed8aac;

		transition: linear .2s;
	}
	&:before {
		width: 20px;
		height: 4px;
		margin-top: -2px;
		margin-left: -10px;
	}
	&:after {
		height: 20px;
		width: 4px;
		margin-top: -10px;
		margin-left: -2px;
	}
}
.toggles__body {
	display: none;
	&.toggles__body_visible {
		display: block;
	}
}
.toggle__content {
	& + & {
		margin-top: 40px;
	}
	.guide-care-galery {
		margin-top: 40px;
		margin-bottom: 10px;
	}
	p {
		line-height: 24px;
		letter-spacing: normal;
		text-transform: none;
		margin-bottom: 10px;
	}
}
.toggle__button_pink {

}

.toggle__list {
	// padding-left: 25px;
	margin-bottom: 20px;
	li {
		padding-left: 30px;
		padding-right: 30px;
		margin-bottom: 0;
		line-height: 1.5;
		&:before {
			top: 11px;
		}
	}
}

.toggle__content-inner {
	// padding-right: 185px;
}

.toggle__content-img {
	width: 224px;
	height: 326px;
	// position: absolute;
	// top: -30px;
	// right: 0;
	float: right;
	position: relative;
	top: -20px;
	@include msm() {
		margin-left: auto;
		margin-right: auto;
		float: none;
		top: auto;
		display: block;
		margin-bottom: 20px;
	}
}

.toggle__content-txt {
	font-family: $gil;
	font-size: 16px;
	font-weight: 700;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.5;
	letter-spacing: normal;
	color: #000000;
	margin-bottom: 10px;
}


.toggle__subTitle {
	font-family: $gil;
	font-size: 16px;
	font-weight: 700;
	line-height: 24px;
	color: #000000;

	position: relative;
	padding-left: 80px;
	margin-bottom: 10px;
	@include msm() {
		padding-left: 50px;
	}
}
.toggle__subTitleIcon {
	width: 44px;
	height: 60px;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-size: contain;
	position: absolute;
	top: 0;
	left: 0;
}

