.list-icons {
	li {
		font-family: $gil;
		font-size: 16px;
		font-weight: bold;
		line-height: 24px;
		color: #000000;

		position: relative;
		margin-bottom: 0;
		padding-left: 80px;
		padding-top: 10px;
		padding-bottom: 10px;
		@include msm() {
			padding-left: 50px;
		}
	}
	& + .toggle__content-txt {
		margin-top: 20px;
	}
}
.list-icons__icon {
	display: block;

	position: absolute;
	top: 50%;
	left: 10px;
	transform: translate(0, -50%);
	width: 37px;
	height: 35px;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-size: contain;
	@include msm() {
		left: 0;
	}
}