.actions-panel {
	padding-left: 15px;
	padding-right: 15px;
}
.actions-panel__profile-link {
	display: block;
	width: 22px;
	transition: linear .1s;
	&:hover {
		opacity: .7;
	}
}

.actions-panel {
	.row {
		& > .paddings {
			@include msm() {
				padding-left: 7px;
				padding-right: 7px;
			}
		}
	}
}