.product-anchor {
	position: relative;
	z-index: 9999;
	transition: ease .1s;
	ul {
		border: none;
	}
	li {
		padding-left: 19px;
		padding-right: 19px;
		flex: 0 0 330px;
		max-width: 330px;
		width: 100%;
		margin-bottom: 10px;
		@include mxl() {
			width: auto;
			max-width: none;
			flex: 0 1 auto
		}
		@include mlg() {
			padding-left: 10px;
			padding-right: 10px;
		}
		@include mmd() {
			width: 100%;
			margin-bottom: 0;
		}
	}
	a {
		display: block;
		border: solid 1px #ed8aac;
		padding: 10px;

		font-family: $gil;
		font-size: 18px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: 2px;
		text-align: center;
		color: #7d6a6a;
		border-radius: 0;
		text-transform: uppercase;
		// @include rectangle-out();
		background-color: #fff;
		&:before {
			display: none;
		}
		@include md() {
			&:before {
				display: block;
			}
		}
		&:hover {
			color: #fff;
			text-decoration: none;
			background-color: #ed8aac;
			@include md() {
				color: #fff;
				border: solid 1px #ed8aac;
				&:before {
					background-color: #ed8aac;
				}
			}
		}
		&.active {
			background-color: #ed8aac;
			color: #fff;
		}
		@include mlg() {
			font-size: 14px;
		}

	}
	&.fixed {
		position: fixed;
		left: 0;
		width: 100% !important;
		z-index: 999;

		background-color: #fff;
		padding: 10px 0 0;
		border-bottom: 1px solid transparent;
		box-shadow: 0 8px 5px -7px rgba(0,0,0,0.7);
		@include mmd() {
			padding-bottom: 10px;
		}
		ul {
			@include mmd() {
				max-width: 540px;
				margin-left: auto;
				margin-right: auto;
			}
		}
		a {

		}
	}

}
