.guide-video {
	padding: 45px 0;
}
.guide-video__title {
	margin-bottom: 35px;
	h2 {
		margin-bottom: 23px;
	}
}
.guide-video__list {
	margin-right: -15px;
	margin-left: -15px;
}
.guide-video__item {
	float: left;
	padding-left: 15px;
	padding-right: 15px;
	width: 100%;
	@include sm() {
		width: 50%;
	}
	@include lg() {
		width: 30%;
		height: 267px;
		margin-bottom: 4px;
	}
	.guide-video__iframe {
		@include lg() {
			height: 210px;
		}
	}
	&:nth-child(3n-2) {
		width: 100%;
		@include lg() {
			height: 537px;
			width: 70%;
		}
		.guide-video__iframe {
			height: 300px;
			@include md() {
				height: 360px;
			}
			@include lg() {
				height: 480px;
			}
		}
	}
}
.guide-video__content {
	p {
		font-family: Gilroy;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		color: #000000;
	}
}

.guide-video__iframe {
	margin-bottom: 10px;
	width: 100%;
	.ytp-chrome-top {
		display: none !important;
	}
}