.new-checkbox {
	position: relative;
}
.new-checkbox__input {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	top: 0;
	left: -9999px;
	&:checked {
		& + .new-checkbox__label {
			&:after {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}
.new-checkbox__label {
	position: relative;
	padding-left: 35px;

	font-family: $gil;
	font-size: 12px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.5;
	letter-spacing: normal;
	text-align: center;
	color: #797979;

	cursor: pointer;
	&:before {
		content: '';

		width: 20px;
		height: 20px;
		background-color: #f7f7f9;
		border: solid 1px #cecece;

		position: absolute;
		top: 50%;
		left: 0;
		transform: translate(0, -50%);
	}
	&:after {
		content: '';

		width: 10px;
		height: 8px;

		background: url('../images/check-black.svg') 50% 50% no-repeat;
		background-size: 10px 8px;

		position: absolute;
		top: 50%;
		left: 5px;
		transform: translate(0, -50%);

		opacity: 0;
		visibility: hidden;
		transition: linear .1s;
	}
}