.color-carousel {
	.owl-nav {
		[class*='owl-'] {
			&:hover {
				color: #000;
			}
		}
		.owl-prev {
			left: -20px;
		}
		.owl-next {
			right: -20px;
		}
	}
}

.color-carousel__item {
	display: block;
	padding-top: 20px;
	img {
		max-width: 120px;
		min-height: 460px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 15px;
		transition: ease .2s;
	}
	&:hover {
		text-decoration: none;
		img {
			transform: scale(1.05, 1.05);
		}
	}
}
.color-carousel__name {
	font-family: $gil;
	font-size: 18px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: normal;
	letter-spacing: 2.1px;
	text-align: center;
	color: #000000;
	text-transform: uppercase;
	margin-bottom: 25px;
}
