.radio {
	position: relative;
}
.radio__input {
	position: absolute;
	top: 0;
	left: -9999px;
	&:checked {
		& + .radio__label {
			&:after {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}
.radio__label {
	cursor: pointer;

	font-family: Gilroy;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.83;
	letter-spacing: 2px;
	text-align: center;
	color: rgba(0, 0, 0, 0.25);
	text-transform: uppercase;
	padding-left: 40px;

	margin: 0;
	display: block;
	&:before {
		content: '';
		display: block;

		width: 24px;
		height: 24px;

		border: solid 1px #e1e1e1;
		border-radius: 50%;

		position: absolute;
		top: 50%;
		left: 15px;
		margin-top: -14px;
	}
	&:after {
		content: '';

		width: 12px;
		height: 12px;
		border-radius: 50%;
		background-color: #ed8aac;
		position: absolute;
		top: 50%;
		left: 21px;
		margin-top: -8px;

		transition: linear .1s;
		opacity: 0;
		visibility: hidden;
	}
}