.button {
	display: block;
	max-width: 173px;
	width: 100%;
	background-color: #ed8aac;
	border: solid 1px #979797;
	padding: 15px 10px 14px;

	font-family: $gil;
	font-size: 12px;
	font-weight: 600;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.83;
	letter-spacing: 3px;
	text-align: center;
	color: #ffffff;
	text-transform: uppercase;

	cursor: pointer;
	transition: linear .1s;
	&:hover {
		// color: #fff;
		text-decoration: none;
	}
}

.button_regular {
	border-color: #ed8aac;
	&:before {
		background-color: #000;
		top: -1px;
		right: -1px;
		bottom: -1px;
		left: -1px;
	}
	&:hover {
		color: #fff;
		&:before {
			background-color: #000;
		}
	}
}

.button_center {
	margin-left: auto;
	margin-right: auto;
}