.salon-preview {
	display: block;
	position: relative;
	color: #000;
	&:hover {
		color: #000;
		text-decoration: none;
		.salon-preview__inner {
			opacity: 1;
			visibility: visible;
		}
	}
}

.salon-preview__img {
	width: 100%;
	@include md() {
		height: 364px;
	}
}
.salon-preview__inner {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;


	background-color: rgba(241,92,136,0.7);
	padding: 20px;

	opacity: 0;
	visibility: hidden;
	transition: ease .2s;
	z-index: 2;
}
.salon-preview__title {
	// margin-bottom: 70px;
	h1, h2, h3, h4, h5, h6, div, p {
		color: #fff;
	}
	h2 {
		font-size: 40px;
		line-height: 45px;
	}
}
.salon-preview__button {
	background-color: #000;
	border-color: #000;
	&:before {
		background-color: #000;
	}
}