.map-list {
	height: 525px;
	overflow: auto;
	@include mxl() {
		height: 435px;
	}
	li {
		padding-bottom: 20px;
		border-bottom: 1px dashed #d7d7d7;
		margin-bottom: 20px;
		cursor: pointer;
		position: relative;
		&:before, &:after {
			opacity: 0;
			visibility: hidden;
			transition: ease .2s;

			position: absolute;
			top: 50%;
			transform: translate(0, -50%);
		}
		&:before {
			content: '';

			width: 16px;
			height: 61px;
			background-color: #ed8aac;

			right: 10px;

		}
		&:after {
			content: '\f054';
			font-family: $fa;
			
			right: 12px;
			color: #fff;

			background-size: contain;
			nask-size: 7px 17px;

		}
		&:hover {
			&:before, &:after {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}
.map-list__item {
	position: relative;
	padding-left: 60px;
	color: #000;
	display: block;
	max-width: 320px;
	&:hover {
		color: #000;
		text-decoration: none;
	}
}
.map-list__count {
	font-family: Gilroy;
	font-size: 16px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.5;
	letter-spacing: normal;
	color: #fff;
	text-transform: uppercase;

	background-color: #ed8aac;

	width: 38px;
	height: 38px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;

	position: absolute;
	top: 0;
	left: 0;
}

.map-list__content {
	h3 {
		font-family: Gilroy;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		color: #000000;
		text-transform: uppercase;
		margin: 0;
	}
	h4 {
		font-family: Gilroy;
		font-size: 14px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.21;
		letter-spacing: normal;
		color: #000000;
		margin: 0;
	}
	p {
		font-family: Gilroy;
		font-size: 12px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.42;
		letter-spacing: normal;
		color: rgba(121, 121, 121, 0.6);
		margin: 0;
	}
	div {
		font-family: Gilroy;
		font-size: 12px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.42;
		letter-spacing: normal;
		color: #797979;
	}
	a {
		font-family: Gilroy;
		font-size: 12px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.42;
		letter-spacing: normal;
		color: #f29fba;
	}
}