.products-nav {
	padding: 35px 0;
	ul {

	}
	li {
		padding-left: 15px;
		padding-right: 15px;
		margin-bottom: 10px;
		&:last-child {
			a {
				padding: 7px 30px 7px 10px;
				text-align: right;
				img {
					left: auto;
					right: 10px;
				}
				&:hover {
					img {
						left: auto;
						right: 7px;
					}
				}
			}
		}
	}
	a {
		display: block;
		max-width: 140px;
		font-family: $gil;
		font-size: 12px;
		font-weight: 300;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.17;
		letter-spacing: 2px;
		color: #ffffff;
		text-transform: uppercase;

		padding: 7px 5px 7px 30px;
		background-color: #ed8aac;
		position: relative;
		img {
			position: absolute;

			top: 50%;
			left: 10px;
			transform: translate(0, -50%);
			color: #fff;

			font-size: 20px;
			transition: linear .1s;
		}
		&:hover {
			text-decoration: none;
			img {
				left: 7px;
			}
		}
	}
}