.inspired-box {
	height: 500px;
	padding: 20px;
	display: block;
	overflow: hidden;

	transition: linear .2s;
	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		transition: linear .2s;
	}
	&:hover {
		text-decoration: none;
		color: #fff;
		img {
			transform: scale(1.05, 1.05);
		}
	}
}

.inspired-box__content {
	position: relative;
	z-index: 2;
	h2 {
		@media (max-width: 1499px) {
			font-size: 38px;
		}
		@include mxl() {
			font-size: 40px;
		}
		@include msm() {
			font-size: 34px;
		}
	}
}