.product-preview {
	position: relative;
	display: block;
	color: #000;
	&:hover {
		text-decoration: none;
		color: #000;
		.product-preview__img {
			transform: rotateY(360deg);
			transition: ease 1s;
		}
	}
}
.product-preview__img {
	margin-bottom: 25px;
	display: block;
}
.product-preview__content {
	margin-bottom: 3px;
	p {
		text-transform: none;
		line-height: 30px;
		letter-spacing: normal;
	}
}
.product-preview__more {
	font-family: Poppins;
	font-size: 14px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.43;
	letter-spacing: 1px;
	text-align: center;
	color: #f29fba;

	display: inline-block;
	position: relative;
	padding-right: 12px;
	&:after {
		content: '>';

		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(0, -50%);
		margin-top: 1px;
		transition: ease .2s;
	}
	&:hover {
		&:after {
			right: -3px;
		}
	}
}