.basket-preview {
	width: 400px;
	color: #999;
	// margin-top: 10px;
}
.basket-preview__inner {
	margin-top: 10px;
	background-color: #fff;
	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
	@include mmd() {
		margin-top: 0;
		height: 100%;
	}
}

.basket-preview__item {
	border-bottom: 1px solid #e5e5e5;
	&:last-child {
		margin-bottom: 0;
	}
}
.basket-preview__item_head {
	padding: 10px 10px 10px 15px;
	h3 {
		font-size: 18px;
		line-height: 26px;
		color: #000;
		font-weight: 400;
		margin: 0;
	}
}
.basket-preview__item_body {
	padding-top: 15px;
	padding-bottom: 15px;
}
.basket-preview__item_foot {
	padding: 15px;
}
.basket-preview__close {
	position: absolute;
	top: 50%;
	right: 10px;
	transform: translate(0, -50%) rotate(45deg);

	width: 25px;
	height: 25px;
	&:before {
		width: 25px;
	}
	&:after {
		height: 25px;
	}
}