.search {
}
.search__button {
	width: 22px;
	transition: linear .1s;
	img {
		-webkit-transition: .2s linear;
		-o-transition: .2s linear;
		transition: .2s linear;
		-webkit-transform-origin: 100% 100%;
		-ms-transform-origin: 100% 100%;
		transform-origin: 100% 100%;
	}
	&:hover {
		opacity: .7;
	}
	@include mmd() {
		margin-left: auto;
		display: block;
		position: relative;
		z-index: 2;
	}
}
.search__input {
	border-bottom: 1px solid #000;
	padding: 0 35px 5px 10px;
	cursor: text;

	position: absolute;
	top: 50%;
	right: 10px;
	margin-top: -10px;

	width: 0;
	opacity: 0;
	visibility: hidden;
	@include iph() {
		color: #fff;
		font-weight: 500;
	}
	&:focus {
		outline: none;
	}
	@include mmd() {
		opacity: 1;
		visibility: visible;
		width: 100% !important;
		top: 0;
		left: 0;
		margin-top: 0;
		padding: 5px 35px 5px 10px;
		border-bottom-color: transparent;
		@include iph() {
			color: #000;
		}
	}
}
.search__button_for_mob {
	@include md() {
		display: none;
	}
}
.search__inner {
	@include mmd() {
		padding-top: 5px;
	}
}