.footer {
	background-color: #fff;
	border: solid 1px #e5e5e5;
	padding: 30px 0 60px;
}
.footer__socials {
	margin-bottom: 20px;
}
.footer__sub-menu {
	margin-bottom: 10px;
}
.footer__copy {
	font-family: Gilroy;
	font-size: 11px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 2;
	letter-spacing: 1px;
	text-align: center;
	color: #424242;
	text-transform: uppercase;
}