.brend-example {
	@include xl() {
		padding: 0 20px 0 35px;
	}
}

.brend-example__title {
	margin-bottom: 45px;
	h3 {
		margin-bottom: 0;
	}
	p {
		font-family: $gil;
		font-size: 22px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.82;
		letter-spacing: 2px;
		color: #000000;
		margin: 0;
		text-transform: uppercase;
	}
}
.brend-example__list {
	margin-left: -5px;
	margin-right: -5px;
	li {
		flex: 0 0 20%;
		max-width: 20%;
		width: 100%;
		padding-left: 5px;
		padding-right: 5px;
		margin-bottom: 10px;
		@include mmd() {
			flex: 0 0 50%;
			max-width: 50%;
		}
	}
}