.list-step {
	li {
		position: relative;
		padding-left: 80px;
		margin-bottom: 10px;

		font-family: $gil;
		font-weight: 500;
		font-size: 16px;
		line-height: 24px;
		@include msm() {
			padding-left: 60px;
		}
	}
	.toggle__content-txt {
		font-family: $gil;
		font-size: 16px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		color: #000000;
		text-transform: uppercase;
		margin: 10px 0 20px;
	}
	.toggle__subTitle {
		font-family: $gil;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		color: #000000;
		margin: 10px 0 20px;
		padding-top: 10px;
		padding-bottom: 10px;
		padding-left: 70px;
		@include msm() {
			padding-left: 50px;
		}
	}
	.toggle__subTitleIcon {
		top: 50%;
		transform: translate(0, -50%);
	}
}
.list-step__number {
	position: absolute;
	top: 0;
	left: 0;

	font-family: $gil;
	font-size: 16px;
	font-weight: bold;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.5;
	letter-spacing: normal;
	color: #f29fba;
}