.crazyColor {
	padding: 25px 0 45px;
}
.crazyColor__title {
	margin-bottom: 30px;
	@include msm() {
		h2 {
			font-size: 40px;
		}
	}
}
.crazyColor__list {
	margin-left: -15px;
	margin-right: -15px;
	@include mxl() {
		padding-left: 9px;
		padding-right: 9px;
	}
	li {
		float: left;
		width: 20%;
		padding-left: 6px;
		padding-right: 6px;
		margin-bottom: 12px;
		@include mmd() {
			width: 50%;
		}
		&:first-child {
			// height: 546px;
			width: 40%;
			@include mmd() {
				width: 100%;
				height: auto;
			}
		}
	}
	img {
		transition: ease .2s;
		width: 100%;
		height: 100%;
	}
	a {
		display: block;
		overflow: hidden;
		&:hover {
			img {
				transform: scale(1.1, 1.1);
				opacity: .8;
			}
		}
	}
}