.guide-steps__link {
	font-family: $gil;
	font-size: 16px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.88;
	letter-spacing: normal;
	color: #ed8aac;
	position: relative;
	padding-right: 15px;
	&:after {
		content: '>';

		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(0, -50%);
		transition: ease .2s;
	}
	&:hover {
		color: #ed8aac;
		text-decoration: none;
		&:after {
			right: -5px;
		}
	}
}