.facts-list {
	padding-left: 15px;
	padding-right: 15px;
	ul {

	}
	li {
		padding-left: 15px;
		padding-right: 15px;
		flex: 0 0 40%;
		max-width: 40%;
		width: 100%;
		margin-bottom: 35px;
		

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		@include mxl() {
			flex: 0 0 50%;
			max-width: 50%;
		}
		@include mmd() {
			flex: 0 1 auto;
			max-width: none;
			margin-bottom: 20px;
		}
	}
}

.fact-item {
	position: relative;
}