.product-prev {
	color: #000;
	position: relative;
	display: block;
	&:hover {
		color: #000;
		text-decoration: none;
		@include md() {
			.product-prev__inner {
				opacity: .1;
			}
			.product-prev__inner-hover {
				opacity: 1;
				visibility: visible;
				transform: rotateX(0);
			}
		}
	}
}
.product-prev__divImg {
	width: 100%;

	background-position: 50% 50%;
	background-size: contain;
	background-repeat: no-repeat;
	margin-bottom: 30px;
	height: 477px;
	transition: ease .2s;
}
.product-prev__example {
	height: 250px;
}
.product-prev__content {
	h3 {
		font-family: Poppins;
		font-size: 18px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.22;
		letter-spacing: 2px;
		text-transform: uppercase;
		margin: 0;
	}
	p {
		font-family: Poppins;
		font-size: 14px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.43;
		letter-spacing: 1px;
		text-align: center;
		margin: 0;
	}
	div {
		font-family: Poppins;
		font-size: 14px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.43;
		letter-spacing: 1px;
	}
}

.product-prev__inner {
	transition: ease .2s;
}

.product-prev__inner-hover {
	opacity: 0;
	visibility: hidden;

	transform: rotateX(90deg);
	transition: linear .2s;

	position: absolute;
	top: 20%;
	left: 0;
	right: 0;
}
.product-prev__button {
	&:before {
		background-color: #000;
	}
	&:hover {
		color: #fff;
		&:before {
			background-color: #000;
		}
	}
}

.product-prev__inner-hover_button {
	top: 50%;
	margin-top: -26px;
}

.product-prev_smaller {
	padding-top: 50px;
	.product-prev__divImg {
		height: 380px;
		margin-bottom: 0;
	}
	&:hover {
		@include md() {
			.product-prev__inner {
				opacity: 1;
			}
		}
	}
}