.galery__item .product-images,
.galery__item .user-view {
  display: none;
}
.user-view-wrap {
	.galery__col {
	  // float: left;
	  // margin: 10px;
	}


	/* 

	Step 2
	======

	Reposition and redesign fancyBox blocks

	*/

	/* This elements contains both blocks */
	.fancybox-inner {
	  position: absolute;
	  top: 0;
	  right: 0;
	  bottom: 0;
	  left: 0;
	  margin: auto;
	  width: calc(100% - 40px);
	  height: calc(100% - 40px);
	  max-width: 1000px;
	  max-height: 800px;
	}

	/* Left block will contain the gallery */
	.fancybox-stage {
	  width: 52%;
	  background: #000;
	}

	/* Right block - close button and the form */
	.fancybox-form-wrap {
	  position: absolute;
	  top: 40px;
	  right: 0;
	  bottom: 40px;
	  width: 48%; 
	  background: #ed8aac;
	}

	/* Add vertical lines */
	.fancybox-form-wrap::before, 
	.fancybox-form-wrap::after {
	  content: '';
	  position: absolute;
	  top: 0;
	  left: 0;
	  bottom: 0;
	}

	.fancybox-form-wrap::before {
	  width: 8px;
	  background: #f4f4f4;
	  display: none;
	}

	.fancybox-form-wrap::after {
	  width: 1px;
	  background: #000;
	}

	/* Set position and colors for close button */
	.fancybox-button--close {
	  position: absolute;
	  top: 0;
	  right: 0;
	  background: transparent;
	  color: #222;
	  padding: 7px;

	}

	.fancybox-button:hover {
	  color: #111;
	  background: transparent;
	}
	.fancybox-button:focus {
	  outline: none;
	}

	.fancybox-button svg path {
	  stroke-width: 1;
	  fill: #fff;
	  stroke: #fff;
	}

	/* Set position of the form */
	.fancybox-inner .user-view {
	  overflow: auto;
	  position: absolute;
	  top: 50px;
	  right: 0;
	  bottom: 50px;
	  left: 0;
	  padding: 0 50px;
	  text-align: center;
	}


	/* 

	Step 3
	======

	Tweak fade animation

	*/

	.fancybox-inner {
	  opacity: 0;
	  transition: opacity .3s;
	}

	&.fancybox-is-open .fancybox-inner {
	  opacity: 1;
	}

	.fancybox-is-closing .fancybox-fx-fade {
	  opacity: 1 !important; /* Prevent double-fading */
	}

	/* 

	Step 2
	======

	Bullet navigation design

	*/
	.product-bullets {
	  list-style: none;
	  position: absolute;
	  bottom: 0;
	  left: 0;
	  width: 100%;
	  text-align: center;
	  margin: 0;
	  padding: 0;
	  z-index: 99999;
	  -webkit-tap-highlight-color: rgba(0,0,0,0);
	}

	.product-bullets li {
	  display: inline-block;
	  vertical-align: top;
	}

	.product-bullets li a {
	  display: block;
	  height: 30px;
	  width: 20px;
	  position: relative;
	}

	.product-bullets li a span {
	  position: absolute;
	  top: 50%;
	  left: 50%;
	  transform: translate(-50%,-50%);
	  width: 10px;
	  height: 10px;
	  border-radius: 99px;
	  text-indent: -99999px;
	  overflow: hidden;
	  background: #fff;
	  box-shadow: 0px 0px 2px rgba(0,0,0,0.5);
	}

	.product-bullets li.active a span {
	  background: #fff;
	}

}


.user-view__content {
	color: #000;
	h1, h2, h3, h4, h5, h6, p, div {
		color: #000;
	}
}