.anchor-menu {
	padding-left: 15px;
	padding-right: 15px;
	transition: ease .1s;
	ul {

	}
	li {
		padding-left: 24px;
		padding-right: 24px;
		position: relative;
		margin-bottom: 10px;
		@include mmd() {
			text-align: center;
			padding-left: 0;
			padding-right: 0;
		}
		&:after {
			@include md() {
				content: '';
				position: absolute;
				top: 50%;
				right: 0;
				width: 3px;
				height: 25px;
				transform: translate(0, -50%);

				background-color: #000;
			}

		}
		&:last-child {
			&:after {
				display: none;
			}
		}
	}
	a {
		display: block;
		position: relative;
		font-family: $gil;
		font-size: 18px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.22;
		letter-spacing: 2px;
		text-align: center;
		color: #000000;
		text-transform: uppercase;

		-webkit-transition: all .2s ease-in-out 0s;
		transition: all .2s ease-in-out 0s;
		@include mxl() {
			font-size: 16px;
		}
		@include mmd() {
			display: inline-block;
		}
		&:before {
			content: "";
			position: absolute;
			width: 100%;
			height: 2px;
			bottom: -5px;
			left: 0;
			background-color: #ed8aac;
			visibility: hidden;
			-webkit-transform: scaleX(0);
			transform: scaleX(0);
			-webkit-transition: all .2s ease-in-out 0s;
			transition: all .2s ease-in-out 0s;

		}
		&:hover {
			text-decoration: none;
			color: #ed8aac;
			// opacity: 1;
			&:before {
				-webkit-transform: scaleX(1);
				transform: scaleX(1);
				visibility: visible;
			}
		}
		&.active {
			color: #ed8aac;
			&:before {
				-webkit-transform: scaleX(1);
				transform: scaleX(1);
				visibility: visible;
			}
		}
	}
	&.fixed {
		position: fixed;
		left: 0;
		width: 100% !important;
		z-index: 1;
		background-color: #fff;
		padding: 12px 0 2px;
		box-shadow: 0 8px 5px -7px rgba(0,0,0,0.7);
	}
}