.logo-section {
	background-color: #ed8aac;
	padding: 10px 0 17px;
	position: relative;
	z-index: 2;
	@include mmd() {
		padding: 10px 0;
	}
}
.logo-section__link {
	flex: 1 1 385px;
	max-width: 385px;
	@include mmd() {
		flex: 1 1 230px;
		max-width: 230px;
		padding-left: 15px;
		padding-right: 15px;
	}
	@media (max-width: 390px) {
		flex: 1 1 160px;
		max-width: 160px;
	}
}
.logo-section__panel {
	position: absolute;
	top: 50%;
	right: 0;
	transform: translate(0, -50%);
	@include mmd() {
		transform: none;
		margin-top: -12px;
	}
	@media (max-width: 575px) {
		// position: static;
		// margin-top: 10px;
	}
}

.logo-section__hamburger {
	padding: 5px;
	width: 36px;
	height: 30px;
	@include md() {
		 display: none;
	}
	.hamburger-box {
		width: 26px;
		height: 20px;
	}
	.hamburger-inner {
		width: 26px;
		height: 4px;
		background-color: #fff;
		&:before, &:after {
			width: 26px;
			height: 4px;
			background-color: #fff;
		}
		&:before {
			top: -8px;
		}
		&:after {
			bottom: -8px;
		}
	}
}

.logo-section__search {
	position: absolute;
	right: 110px;
	top: 50%;
	transform: translate(0, -50%);
	transition: .2s opacity ease 0s,
				.2s visibility ease 0s,
				.2s top ease .2s;
	@include mmd() {
		z-index: -1;
		background-color: #fff;
		top: 0;
		transform: translate(0, 0);
		left: 0;
		right: 0;
		margin-top: 10px;
		// display: none;

		opacity: 0;
		visibility: hidden;

		&:before {
			content: '';

			position: absolute;
			top: 0;
			left: -9999px;
			right: -9999px;
			height: 35px;

			background-color: #fff;
			border-bottom: 1px solid #000;
		}
	}
	&.search_active {
		opacity: 1;
		visibility: visible;

		transition: .2s opacity ease .1s,
					.2s visibility ease .1s,
					.2s top ease 0s;
		@include mmd() {
			top: 100%;
		}
		.search__button {
			z-index: 999;
		}
		.search__input {
			width: 200px;
			opacity: 1;
			visibility: visible;
			transition: linear .2s;
			@include mmd() {
				// width: 100%;
			}
			&:focus {
				& + {
					.search__button {
						img {
							animation-name: search;
							animation-duration: 4s;
							animation-iteration-count: infinite;
						}
					}
				}
			}
		}
	}
}