.similar-carousel {
	.owl-stage {
		padding: 20px 0;
	}
	.owl-nav {
		top: 40%;
		[class*="owl-"] {
			&:hover {
				color: #000;
			}
		}
	}
	.owl-dots {
		position: static;
		transform: none;
		margin-top: 20px !important;
	}
}

.similar-carousel__product {
	.product-prev__divImg {
		height: 395px;
	}
	.product-prev__content {
		h3 {
			font-family: $gil;
			font-size: 18px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.22;
			letter-spacing: 2px;
			text-align: center;
			color: #000000;
			margin-bottom: 10px;
		}
		p {
			font-family: $gil;
			font-size: 14px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.57;
			letter-spacing: normal;
			text-align: center;
			color: #000000;
		}
		div {
			font-family: $gil;
			font-size: 14px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.21;
			letter-spacing: normal;
			text-align: center;
			color: #000000;
		}
	}
	&:hover {
		.product-prev__inner {
			@include md() {
				opacity: 1;
			}
		}
		.product-prev__divImg {
			opacity: .9;
			transform: scale(1.08, 1.08);
		}
	}
}