.upload-foto {
	position: relative;
}
.upload-foto__input {
	position: absolute;
	top: 0;
	left: -9999px;
	opacity: 0;
	visibility: hidden;
}
.upload-foto__label {
	margin-bottom: 0;
	display: block;
	cursor: pointer;
	// max-width: 325px;
	padding: 11px 10px 10px;


	font-family: $gil;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 22px;
	letter-spacing: 2px;
	text-align: center;
	color: #ffffff;
	text-transform: uppercase;

	background-color: #ed8aac;
	border: solid 1px #ed8aac;
	&:before {
		background-color: #000;
		top: -1px;
		right: -1px;
		bottom: -1px;
		left: -1px;
	}
	&:hover {
		color: #fff;
		&:before {
			background-color: #000;
		}
	}
}