.instagram-preview {
	position: relative;
	display: block;
	color: #000;
	overflow: hidden;
	&:hover {
		text-decoration: none;
		color: #000;
		.instagram-preview__img {
			transform: scale(1.1, 1.1);
			opacity: .8;
		}
		.instagram-preview__inner {
			transform: rotateY(0);
			opacity: 1;
			visibility: visible;
		}
	}
}

.instagram-preview__img {
	display: block;
	width: 100%;

	transition: linear .3s;
	transform-origin: 0 0;
	@include md() {
		height: 266px;
	}
}

.instagram-preview__inner {
	padding: 40px 20px 20px;

	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;

	background-color: rgba(0,0,0,0.8);
	color: #fff;

	opacity: 0;
	visibility: hidden;
	transition: linear .2s;
	transform: rotateY(90deg);
}

.instagram-preview__item {
	span {
		padding-left: 3px;
		padding-right: 3px;
	}
}
.instagram-preview__txt {
	margin-top: 10px;
	font-size: 14px;
	line-height: 18px;
}