.menu {
	border-top: 1px solid #000;
	border-bottom: 1px solid #000;
	padding: 14px 15px;
	position: relative;
	z-index: 1;
	background-color: #fff;
	transition: ease .1s;
	
	@include mmd() {
		opacity: 0;
		visibility: hidden;
		position: fixed;
		top: 50px;
		left: 0;
		bottom: 0;
		right: 0;
		padding: 0;

		border: none;


		background-color: rgba(#000, .7);
		transition: ease .3s;
	}
	&.menu_open {
		opacity: 1;
		visibility: visible;
		transition: ease .3s;
		.menu__list {
			transform: translate(0, 0);
			transition: ease .6s;
		}
	}
	.menu__socials {
		padding: 0;
		background-color: rgb(238, 238, 238);
		// transition: linear .2s;
		li {
			padding: 11px 10px;
			border-bottom: none;
		}
		a {
			&:hover {
				opacity: .8;
			}
		}
		@include md() {
			display: none;
		}
	}
	&.fixed {
		@include md() {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;

			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			background-color: #fff;
			padding-top: 10px;
			padding-bottom: 11px;
			border-top-color: transparent;
			border-bottom-color: #adadae;
			@include mxl() {
				padding-top: 9px;
				padding-bottom: 9px;
			}
			@include mlg() {
				padding-top: 8px;
			}
		}
		.menu__list {
			li {
				@media (max-width: 1050px) {
					padding-left: 5px;
					padding-right: 5px;
				}
			}
		}
		.menu__logo {
			display: block;
			@media (max-width: 900px) {
				display: none;
			}
		}
		.menu__actions-panel {
			display: block;
			padding: 0;
		}
	}
}
