.menu__list {
	@media only screen and(min-width: 1500px) {
		max-width: 1140px;
		margin-left: auto;
		margin-right: auto;
		justify-content: space-between !important;
	}
	@include mmd() {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		max-width: 570px;
		width: 100%;

		background-color: #fff;

		display: block !important;
		text-align: left;
		margin-right: 0;
		margin-left: 0;
		transform: translate(-300px, 0);
		transition: ease .6s;
	}
	li {
		padding-left: 15px;
		padding-right: 15px;
		@include mxl() {
			padding-left: 10px;
			padding-right: 10px;
		}
		@include mmd() {
			padding-left: 5px;
			padding-right: 5px;
			border-bottom: 1px solid #000;
			padding: 21px 10px;
		}
	}
	a {
		display: block;
		position: relative;

		font-weight: 500;
		line-height: 1.13;
		text-align: center;
		color: #424242;
		text-transform: uppercase;
		-webkit-transition: all .2s ease-in-out 0s;
		transition: all .2s ease-in-out 0s;
		@include mxl() {
			font-size: 14px;
		}
		@include mlg() {
			font-size: 13px;
		}
		@include mmd() {
			font-size: 16px;
			text-align: left;
		}
		&:before {
			content: "";
			position: absolute;
			width: 100%;
			height: 2px;
			bottom: -2px;
			left: 0;
			background-color: #ed8aac;
			visibility: hidden;
			-webkit-transform: scaleX(0);
			transform: scaleX(0);
			-webkit-transition: all .2s ease-in-out 0s;
			transition: all .2s ease-in-out 0s;

		}
		&:hover {
			text-decoration: none;
			opacity: .8;
			@include md() {
				color: #ed8aac;
				opacity: 1;
				&:before {
					-webkit-transform: scaleX(1);
					transform: scaleX(1);
					visibility: visible;
				}
			}
		}
		&.active {
			opacity: .8;
			@include md() {
				color: #ed8aac;
				&:before {
					-webkit-transform: scaleX(1);
					transform: scaleX(1);
					visibility: visible;
				}
			}
		}
	}
}




.menu__logo {
	display: none;
	max-width: 100px;
	flex: 1 1 100px;
}

.menu__actions-panel {
	display: none;
}