.basket {
	@include md() {
		perspective: 200px;
	}
	// position: fixed;
	// top: 0;
	// right: 0;
}
.basket_active {
	.basket__preview {
		opacity: 1;
		visibility: visible;
		@include md() {
			transform: rotateX(0);
		}
		@include mmd() {
			transform: translate(0, 0);
		}

		// -webkit-transition: .3s cubic-bezier(.75, .25, .25, .75);
		// -moz-transition: .3s cubic-bezier(.75, .25, .25, .75);
		// -ms-transition: .3s cubic-bezier(.75, .25, .25, .75);
		// -o-transition: .3s cubic-bezier(.75, .25, .25, .75);
		// transition: .3s cubic-bezier(.75, .25, .25, .75);
	}
}
.basket__link {
	display: block;
	width: 22px;
	transition: linear .1s;
	&:hover {
		opacity: .7;
	}
}

.basket__preview {
	position: absolute;
	right: 0;
	top: 100%;

	opacity: 0;
	visibility: hidden;
	@include md() {
		transform-origin: 50% 0;
		transform: rotateX(-90deg);
	}
	@include mmd() {
		transform: translate(200px, 0);
	}
	-webkit-transition: .2s cubic-bezier(.75, .25, .25, .75);
	-moz-transition: .2s cubic-bezier(.75, .25, .25, .75);
	-ms-transition: .2s cubic-bezier(.75, .25, .25, .75);
	-o-transition: .2s cubic-bezier(.75, .25, .25, .75);
	transition: .2s cubic-bezier(.75, .25, .25, .75);
	@include mmd() {
		position: fixed;
		top: 0;
		right: 0;
		margin: 0;
		bottom: 0;
		width: 100%;
		max-width: 320px;
		// width: 300px;
		z-index: 999;
		// width: 500px;
		// bottom: 0;
	}
}