.galery-form {
	padding: 20px 0 44px;
}
.galery-form__row {
	margin-left: -10px;
	margin-right: -10px;
	margin-bottom: 35px;
}
.galery-form__item {
	margin-bottom: 10px;
	position: relative;
}
.galery-form__input {
	width: 100%;
	font-family: $gil;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1;
	letter-spacing: 2px;
	color: #aeaeae;
	text-transform: uppercase;

	background-color: #f7f7f9;
	border: solid 1px #d7d7d7;
	padding: 12px 15px;
	transition: linear .1s;
	@include iph() {
		font-family: $gil;
		font-size: 12px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1;
		letter-spacing: 2px;
		color: #aeaeae;
		text-transform: uppercase;
	}
	&:focus {
		outline: none;
		border-color: #000;
	}
}

.galery-form__radio-wrap {
	padding-top: 10px;
}
.galery-form__radio {
	opacity: 0;
	visibility: hidden;
}

.galery__form {

}



.form-error {
	background-color: #de4242;
	border: solid 1px #de4242;
	font-family: $gil;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: normal;
	letter-spacing: normal;
	text-align: center;
	color: rgba(255, 255, 255, 0.87);
	padding: 5px 10px;
	text-align: left;
}
.form-succes {
	background-color: green;
	border: solid 1px green;
	font-family: $gil;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: normal;
	letter-spacing: normal;
	text-align: center;
	color: rgba(255, 255, 255, 0.87);
	padding: 5px 10px;
	text-align: left;
}

.galery-form__note-pos {
	position: absolute;
	top: 100%;
	left: 15px;
	right: 15px;
	z-index: 9999;
}

.galery__item-upl {
	.upload-foto__label {
		font-family: $gil;
		font-size: 12px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.83;
		letter-spacing: 2px;
		color: #aeaeae;
		text-transform: uppercase;
		text-align: left;

		background-color: #f7f7f9;
		border: solid 1px #d7d7d7;
		padding: 12px 15px;
	}
}