.help-links {
	background-color: #000;
	padding: 6px 0;
	height: 30px;
	overflow: hidden;
	@include mmd() {
		display: none;
	}
}
.help-links__link {
	display: block;
	font-size: 10px;
	line-height: 1.8;

	font-weight: 500;
	color: #fff;
	text-transform: uppercase;
	text-decoration: none;
	&:hover {
		opacity: .8;
		color: #fff;
		text-decoration: none;
	}
}