.close-icon {
	display: block;
	width: 20px;
	height: 20px;
	transform: rotate(45deg);
	font-size: 0;
	line-height: 1;
	&:before, &:after {
		content: '';

		background-color: #000;
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
	&:before {
		width: 20px;
		height: 2px;
	}
	&:after {
		width: 2px;
		height: 20px;
	}
}