.product-descr {

}
.product-descr__crumbs {

}
.product-descr__title {
	margin-bottom: 25px;
}
.product-descr__info {
	margin-bottom: 45px;
	h4 {
		font-family: $gil;
		font-size: 16px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		text-align: center;
		color: #000000;
	}
	p {
		max-width: 520px;
		font-family: $gil;
		font-size: 14px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		text-align: center;
		color: #000000;
		margin-left: auto;
		margin-right: auto;
	}
	img {
		max-width: 150px;
		margin-bottom: 15px;
	}
}

.product-descr__options {
	margin-bottom: 50px;
}
.product-descr__anchors {
	a {
		font-family: Gilroy;
		font-size: 13px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.69;
		letter-spacing: 2px;
		text-align: center;
		color: #000000;
	}
}

.product-descr__content {
	margin-bottom: 40px;
	div {
		padding-top: 10px;
		display: none;
		font-family: Gilroy;
		font-size: 14px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.21;
		letter-spacing: normal;
		text-align: center;
		color: #000000;
		max-width: 480px;
		margin-left: auto;
		margin-right: auto;
	}
}