.modal-template {
	padding: 30px;
	background-color: #fff;
	max-width: 755px;
	margin-left: auto;
	margin-right: auto;
	display: none;
}
.modal-template__title {
	margin-bottom: 20px;
	h2 {
		margin-bottom: 20px;

	}
	p {
		font-family: $gil;
		font-size: 14px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.21;
		letter-spacing: normal;
		text-align: center;
		color: #9a9a9a;
	}
}
.modal-template__col {
	margin-bottom: 20px;
}
.modal-template__content {
	max-width: 640px;
	margin-left: auto;
	margin-right: auto;
	p {
		font-family: Gilroy;
		font-size: 14px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.21;
		letter-spacing: normal;
		text-align: center;
		color: #000000;
	}
}