.subscribe {
	padding: 40px 0;
}
.subscribe__title {
	margin-bottom: 20px;
	h2 {
		margin-bottom: 30px;
	}
	p {
		margin-bottom: 15px;
	}
}
.subscribe__title-txt {
	max-width: 600px;
	margin-left: auto;
	margin-right: auto;
	a {
		color: #000;
		text-decoration: underline;
		&:hover {
			color: #000;
			text-decoration: none;
		}
	}
}
.subscribe__form {
	margin-bottom: 15px;
}

.subscribe__links {
	li {
		position: relative;
		padding-left: 10px;
		padding-right: 10px;
		&:after {
			content: '|';

			position: absolute;
			right: -1px;
			top: 50%;
			transform: translate(0, -50%);
			font-size: 16px;
			line-height: 1.5;
			color: #000;
		}
		&:last-child {
			&:after {
				display: none;
			}
		}
	}
}
.subscribe__link {
	font-family: $gil;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.5;
	letter-spacing: normal;
	text-align: center;
	color: #797979;
	position: relative;
	&:before {
		content: '';

		position: absolute;
		bottom: -5px;
		left: 0;
		width: 100%;
		height: 1px;

		background-color: #797979;

		opacity: 0;
		visibility: hidden;

		transition: ease .1s;
	}
	&:hover {
		text-decoration: none;
		color: #797979;
		&:before {
			bottom: -1px;
			opacity: 1;
			visibility: visible;
			
		}
	}
}