.modal-form {

}
.modal-form__wrap {
	margin-bottom: 10px;
}
.modal-form__input {

}
.modal-form__textarea {
	resize: none;
}
.modal-form__textarea, .modal-form__input {
	width: 100%;

	font-family: Gilroy;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.83;
	letter-spacing: 2px;
	color: #000;
	text-transform: uppercase;
	background-color: #f7f7f9;
	border: 1px solid #f7f7f9;

	padding: 12px 15px;
	transition: linear .1s;
	@include iph() {
		font-family: Gilroy;
		font-size: 12px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.83;
		letter-spacing: 2px;
		color: #aeaeae;
		text-transform: uppercase;
	}
	&:focus {
		outline: none;
		border-color: #000;
	}
}
.modal-form__recaptcha {
	max-width: 305px;
	margin-bottom: 10px;
}

.modal-form__button {
	
}

.fancybox-slide {
	transform: translate(0, -100px);
}
.fancybox-slide--current {
	transform: translate(0, 0);
}