.regular-list {
	list-style: none;
	margin-bottom: 5px;
	li {
		position: relative;
		padding-left: 40px;
		margin-bottom: 5px;

		font-family: $gil;
		font-size: 16px;
		font-weight: 500;
		line-height: 24px;
		color: #000000;
		&:before {
			content: '';

			position: absolute;
			left: 5px;
			top: 13px;

			display: block;
			width: 4px;
			height: 4px;
			border-radius: 50%;

			background-color: #ed8aac;

		}
	}
}