.product-carousel {
	.owl-nav {
		.owl-prev {
			left: -15px;
			@include md() {
				left: 0;
			}
		}
		.owl-next {
			right: -15px;
			@include md() {
				right: 0;
			}
		}
		[class*='owl-'] {
			&:hover {
				color: #000;
			}
		}
	}
	img {
		max-width: 550px;
		display: block;
		margin-left: auto;
		margin-right: auto;
	}
}