.purchase-option {
	position: relative;
	p {
		margin-bottom: 15px;
	}
}
.purchase-option__label {
	background-color: #fff;
	border: solid 1px #b2b3b3;
	position: relative;

	padding: 11px 20px 11px 20px;
	display: block;
	cursor: pointer;
	margin-bottom: 0;
	transition: linear .2s;
	text-transform: uppercase;
	&:hover {
		// padding-left: 50px;
		.purchase-option__pseudo {
			opacity: 1;
			visibility: visible;
			left: 10px;
			transition: linear .2s;
			& + div {
				padding-left: 30px;
			}
		}
	}
}
.purchase-option__input {
	position: absolute;
	top: 0;
	left: -9999px;
	opacity: 0;
	visibility: hidden;
	& + .purchase-option__label {
		.purchase-option__pseudo {
			& + div {
				transition: linear .2s;
			}
		}
	}
	&:checked {
		& + .purchase-option__label {
		background-color: rgba(0, 0, 0, 0.03);
			.purchase-option__pseudo {
				opacity: 1;
				visibility: visible;
				left: 10px;
				& + div {
					padding-left: 30px;
				}
			}
		}
	}
}
.purchase-option__pseudo {
	position: absolute;
	top: 50%;
	left: -10px;
	transform: translate(0, -50%);

	width: 29px;
	height: 23px;
	background-size: 29px 23px;
	mask-size: 29px 23px;

	background:  url("../images/check.svg") 50% 50% no-repeat;
	opacity: 0;
	visibility: hidden;

	transition: left linear .2s,
				opacity linear .05s,
				visibility linear .05s;
}
.purchase-option__value {
	font-family: Gilroy;
	font-size: 16px;
	font-weight: 500;
	line-height: 1.13;
	letter-spacing: 1px;
	color: #424242;
	@include mxl() {
		font-size: 14px;
	}
	@include mlg() {
		font-size: 16px;
	}
	// text-transform: uppercase;
	
}
.purchase-option__price {
	font-family: Poppins;
	font-size: 24px;
	font-weight: 400;
	line-height: 0.83;
	letter-spacing: 1px;
	color: #f29fba;
	// text-transform: uppercase;
	@include mxl() {
		font-size: 20px;
	}
	@include mlg() {
		font-size: 24px;
	}
}
.purchase-option__submit {
	max-width: 295px;
	margin-top: 17px;
}
.purchase-option__check-wrap {
	margin-bottom: 10px;
}