.fact-item {
	position: relative;
	padding-left: 60px;

	display: flex;
	flex-wrap: wrap;
	align-items: center;
	@include mmd() {
		min-height: 45px;
	}
}

.fact-item__icon {
	position: absolute;
	top: 50%;
	left: 0;
	transform: translate(0, -50%);
	background-size: contain;
	background-position: 50% 50%;
	background-repeat: no-repeat;
	width: 45px;
	height: 45px;
}