.galery {

}
.galery_page {
	padding-top: 50px;
}
.galery__section {
	padding: 40px 0;
	border-bottom: 1px solid #eee;
}
.galery__title {
	margin-bottom: 30px;
	h2 {
		margin-bottom: 30px;
	}
}
.galery__list {
	margin-bottom: 20px;
}
.galery__col {
	
}
.galery__item-link {
	color: #000;
	margin-bottom: 18px;
	&:hover {
		color: #000;
		text-decoration: none;
	}
}
.galery__img {
	margin-bottom: 7px;
}
.galery__content {
	
}

.galery__upload {
	max-width: 325px;
	margin-left: auto;
	margin-right: auto;
}
.galery__iframe {
	display: block;
	width: 100%;
	max-width: 823px;
	height: 551px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 45px;
	@include msm() {
		height: 300px;
	}
}
.galery__more-video {
	max-width: 278px;
	border-color: #ed8aac;
}

.galery__more-blog {
	max-width: 176px;
	border-color: #ed8aac;
}

.galery__title_m0 {
	p {
		margin: 0;
	}
}

.galery__form {
	max-width: 800px;
	margin-left: auto;
	margin-right: auto;
}