.instagram {
	padding: 40px 0 45px;
}
.instagram__title {
	margin-bottom: 30px;
	h2 {
		margin-bottom: 30px;
	}
}
.instagram__list {
	@include md() {
		margin-left: -5px;
		margin-right: -5px;
	}
}
.instagram__col {
	width: 100%;
	padding-left: 15px;
	padding-right: 15px;
	margin-bottom: 15px;
	@include md() {
		padding-left: 5px;
		padding-right: 5px;
		margin-bottom: 10px;
	}
	@include md() {
		flex: 0 0 50%;
		max-width: 50%;
	}
	@include lg() {
		flex: 0 0 33.3%;
		max-width: 33.3%;
	}
	@include xl() {
		flex: 0 0 25%;
		max-width: 25%;
	}
	@media (min-width: 1500px) {
		flex: 0 0 20%;
		max-width: 20%;
		
	}
}