.subscribe-form {

}
.subscribe-form__input {
	flex: 1 1 302px;
	max-width: 302px;
	// height: 50px;
	background-color: #000000;
	// border: solid 1px #979797;
	border-color: transparent;

	font-family: $gil;
	font-size: 12px;
	font-weight: 600;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.25;
	letter-spacing: 2px;
	color: #ffffff;

	padding: 16px 20px 15px;
	@include iph() {
		font-family: $gil;
		font-size: 12px;
		font-weight: 600;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.25;
		letter-spacing: 2px;
		color: #ffffff;
		text-transform: uppercase;
	}
	&:focus {
		outline: none;
	}
	@include mmd() {
		flex: 1 1 100%;
		max-width: 100%;
		@include iph() {
			text-align: center;
		}
	}
}
.subscribe-form__button {
	flex: 1 1 143px;
	max-width: 143px;
	padding-top: 14px;
	padding-bottom: 13px;
	border-color: transparent;

	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.83;
	letter-spacing: 2px;
	transition: linear .1s;
	&:hover {
		background-color: #000;
	}
	@include mmd() {
		flex: 1 1 100%;
		max-width: 100%;
	}
}