.sub-menu {
	ul {
		margin-left: -15px;
		margin-right: -15px;
	}
	li {
		padding-left: 15px;
		padding-right: 15px;
		margin-bottom: 10px;
	}
	a {
		display: block;
		position: relative;

		font-family: $gil;
		font-size: 13px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: 1px;
		text-align: center;
		color: #424242;

		-webkit-transition: all .2s ease-in-out 0s;
		transition: all .2s ease-in-out 0s;
		&:before {
			content: "";
			position: absolute;
			width: 100%;
			height: 2px;
			bottom: -2px;
			left: 0;
			background-color: #ed8aac;
			visibility: hidden;
			-webkit-transform: scaleX(0);
			transform: scaleX(0);
			-webkit-transition: all .2s ease-in-out 0s;
			transition: all .2s ease-in-out 0s;

		}
		&:hover {
			text-decoration: none;
			color: #ed8aac;
			// opacity: 1;
			&:before {
				-webkit-transform: scaleX(1);
				transform: scaleX(1);
				visibility: visible;
			}
		}
	}
}