.crumbs {
	padding-left: 15px;
	padding-right: 15px;
	position: relative;
	li {
		padding-left: 15px;
		padding-right: 15px;
		position: relative;
		margin-bottom: 10px;
		&:after {
			content: '/';

			position: absolute;
			top: 50%;
			right: -5px;
			transform: translate(0, -50%);

			font-family: $gil;
			font-size: 12px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.83;
			letter-spacing: 2px;
			text-align: center;
			color: #000000;
		}
		&:last-child {
			&:after {
				display: none;
			}
		}
		& > * {
			font-family: $gil;
			font-size: 12px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.83;
			letter-spacing: 2px;
			text-align: center;
			color: #000000;
			text-transform: uppercase;
			&:hover {
				// text-decoration: none;
			}
		}
	}
}