.squad-member {
	margin-bottom: 20px;
	@include mlg() {
		margin-bottom: 40px;
	}
}
.squad-member__img {
	float: left;

	max-width: 460px;
	margin-right: 40px;
	margin-bottom: 20px;
	@include mlg() {
		margin-right: 20px;
	}
	@include mmd() {
		max-width: 100%;
		margin-left: 0;
		margin-right: 0;
		float: none;
	}
}
.squad-member__content {
	// margin-left: 500px;
	margin-bottom: 20px;
	h3 {
		font-family: $gil;
		font-size: 18px;
		font-weight: 500;
		line-height: 22px;
		letter-spacing: 2px;
		color: #000000;
		margin-bottom: 30px;
	}
	p {
		font-family: $gil;
		font-size: 16px;
		line-height: 24px;
		color: #000;
		font-weight: 500;
	}
}