.range-color-box {
	margin-bottom: 30px;
	@include xl() {
		padding-left: 6px;
		padding-right: 6px;
		margin-bottom: 12px;
	}
}
.range-color-box__preview {
	height: 500px;
	display: block;
	padding: 20px 50px;
	overflow: hidden;
	&:hover {
		text-decoration: none;
		color: #fff;
		.range-color-box__img {
			transform: scale(1.05, 1.05);
		}
	}
	@include msm() {
		height: 400px;
	}
}
.range-color-box__img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;

	transition: linear .3s;
}
.range-color-box__content {
	position: relative;
	z-index: 3;
	@include mlg() {
		h2 {
			font-size: 35px;
		}
	}
	@include msm() {
		h2 {
			font-size: 30px;
		}
		p {
			font-size: 14px;
		}
	}
}
.range-color-box__shop {
	display: inline-block;

	width: 173px;
	padding: 15px 10px 14px;
	// height: 52px;
	background-color: #ed8aac;
	border: solid 1px #979797;
	margin-left: auto;
	margin-right: auto;

	font-family: $gil;
	font-size: 12px;
	font-weight: 600;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.83;
	letter-spacing: 3px;
	text-align: center;
	color: #ffffff;
	text-transform: uppercase;
}
