.stockits-form {
	padding-left: 15px;
	padding-right: 15px;
}
.stockits-form__inner {

}
.stockits-form__input {
	font-family: Gilroy;
	font-size: 16px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1;
	letter-spacing: normal;
	color: #000;
	border-radius: 30px;
	background-color: rgba(0, 0, 0, 0);
	border: solid 1px #ebebeb;

	display: block;
	width: 100%;
	max-width: 630px;
	margin-left: auto;
	margin-right: auto;
	padding: 14px 35px;
	margin-bottom: 50px;
	transition: ease .1s;
	@include iph() {
		font-family: Gilroy;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1;
		letter-spacing: normal;
		color: rgba(0, 0, 0, 0.42);
	}
	&:focus {
		outline: none;
		border-color: #000;
	}
}
.stockits-form__col {
	margin-bottom: 30px;
}