.under-header {
	padding: 52px 0 50px;
	position: relative;
	z-index: 1;
}
.under-header_regular {
	padding: 0 0 20px;
	height: 234px;
	display: flex;
	align-items: center;
	background-color: #f7f7f9;
	overflow: hidden;
	// background: #f7f7f9 url("../images/regularUnderHeader.png") 83% 100% no-repeat;
	// // background-size: contain;
	// background-size: 290px 300px;
