// float clear
// ===============

@mixin clearfix() {
	&:before, &:after {
		content: '';
		display: table;
	}
	&:after {
		clear: both;
	}
}

// =============================================
// =============================================

/*   */
// ===============

@mixin list() {
	padding: 0;
	margin: 0;
	list-style: none;
}

// =============================================
// =============================================

// placeholder
// ===============


@mixin iph {
	&.placeholder {
		@content; 
	}
	&:-moz-placeholder {
		@content; 
	}
	&::-moz-placeholder {
		@content; 
	}
	&:-ms-input-placeholder {
		@content; 
	}
	&::-webkit-input-placeholder {
		@content; 
	}
}

