.subscribe-page {

}
.subscribe-page__item {
	margin-bottom: 30px;
}
.subscribe-page__box {
	background-color: #fff;
	border: 1px solid #ddd;
	padding: 40px 40px 30px;
	@include msm() {
		padding: 15px 15px;
	}
}
.subscribe-page__content {
	margin-bottom: 23px;
	h4 {
		font-family: Gilroy;
		font-size: 18px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.22;
		letter-spacing: 2px;
		color: #000000;
		margin-bottom: 15px;
	}
	p {
		font-family: Gilroy;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		color: #000000;
	}
}
.subscribe-page__input {
	font-family: Gilroy;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.83;
	letter-spacing: 2px;
	color: #000;
	text-transform: uppercase;
	transition: linear .1s;

	padding: 12px 15px;
	width: 100%;
	margin-bottom: 10px;

	background-color: #f7f7f9;
	border: solid 1px #979797;
	@include iph() {
		font-family: Gilroy;
		font-size: 12px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.83;
		letter-spacing: 2px;
		color: #aeaeae;
		text-transform: uppercase;
	}
	&:focus {
		outline: none;
		border-color: #000;
	}
}
.subscribe-page__input_out {
	margin-top: 35px;
}
.subscribe-page__button {
	
}
